</template>


<script>
import { getCurrencies } from '@/api'

export default {
  name: 'the-select-token',

  props: {
    label: {
      type: String,
    }
  },

  data() {
    return {
      isSearchOpen: false,
      loading: true,
      selectedCurrency: null,
      currencies: [],
      searchInput: '',
    }
  },

  async created() {
    try {
      this.currencies = await getCurrencies();
      this.selectedCurrency = this.currencies[0];
      this.loading = false;
    } catch (e) {
      console.error(e);
    }
  },

  computed: {
    filteredCurrencies() {
      const searchInput = this.searchInput.toLowerCase();
      return this.searchInput
        ? this.currencies.filter(c => c.name.toLowerCase().includes(searchInput) || c.code.toLowerCase().includes(searchInput))
        : this.currencies;
    },
  },

  methods: {
    openSearch() {
      this.isSearchOpen = true;
    },

    hideSearch() {
      this.isSearchOpen = false;
      this.searchInput = '';
    }
  }
};
</script>

